import { useState } from "react"
import styled from "styled-components"
import { Hjem } from "./Hjem"
import { Toppliste } from "./Toppliste"


const Div = styled.div`
  margin-left: auto;
  margin-right: auto;
  max-width: 400px;
  width: 90%;
`

export type State = 'TOPPLISTE' | 'HJEM'

export const Main = () => {
    const [state, setState] = useState<State>('HJEM')

    return (
        <Div>
            {state === 'TOPPLISTE' && (
                <Toppliste setState={ setState }/>
            )}

            {state === 'HJEM' && (
                <Hjem setState={ setState } />
            )}
        </Div>
    )
}

export default Main