interface TopplisteProps {
    setState: (input: State) => void
}

export const Toppliste = ({ setState }: TopplisteProps) => {

    return (
        <Div>
            <button onClick={ () => setState('HJEM')}><BsArrowLeft/> Gå tilbake til oppgavene</button>
            <h1 style={{marginTop: "30px"}}>Toppliste</h1>
            <h5>Oppdatert: 09/12/2021</h5>
            <Topplist>
                {
                    data.sort(function (x, y) { return y.score - x.score; }).map((it) => (
                        <TopplistElement>
                            <Score><strong>{it.plass}.</strong></Score>
                            <Navn><IconComponent plass={ it.plass }/> {it.navn} </Navn>
                            <Poeng>{it.score}p</Poeng>
                        </TopplistElement>))
                }
            </Topplist>
        </Div>
    )
}

type IconType = {
    plass: number
}

const IconComponent = ({ plass }: IconType) => {
    
    if (plass === 1) {
        return (
            <span style={{color: "#DAA520"}}>
                <BsTrophyFill />
            </span>
        )
    }else if (plass === 2) {
        return (
            <span style={{color: "silver"}}>
                <BsTrophyFill />
            </span>
        )
    }else if (plass === 3) {
        return (
            <span style={{color: "#CD7F32"}}>
                <BsTrophyFill />
            </span>
        )
    } else {
        return null
    }
}